import React from 'react';
import { Grid, List } from 'antd-mobile';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch,Link } from 'react-router-dom'

const data = Array.from(new Array(10)).map((_val, i) => ({
  icon: 'https://gw.alicdn.com/bao/uploaded/i3/112638574/O1CN01l0kiH62DCwRwdNWJA_!!112638574.jpg_500x500q90.jpg_.webp',
  text: `name${i}`,
}));



const Listt = () => (
  <div>


    <div 
        className="sub-title"
        style={{
            color:'darkorange',
            fontSize:16,
            textAlign:'center',
            margin:20
        }}
    > 
        --你可能还喜欢-- 
    </div>
    <Grid 
        data={data} 
        columnNum={2} 
        square={false} 
        className="not-square-grid"
        style={{borderRadius:15}}

        renderItem={dataItem => (
                <div style={{margin:0,padding:0}}>
                      <Link to="/detail">
                        <img src={dataItem.icon} style={{ width: '100%', height: '100%' }} alt="" />
                        <div style={{ 
                            color: '#000', 
                            fontSize: '14px', 
                            marginTop: '10px',
                            lineHeight:'20px',
                            textAlign:"left"
                            }}>
                            <span>粗线毛衣开衫女冬加厚中长款宽松外穿2020年新款慵懒风</span>
                        </div>
                        <div style={{
                            color:'darkorange',
                            textAlign:"left",
                            lineHeight:'20px',
                        }}>
                            <span>￥100</span>
                        </div>
                    </Link>
                </div>  
        )}
    />
  </div>
);

export default Listt;