import { SearchBar } from 'antd-mobile';
import React from 'react'
import Search from './search'
import Mytab from './Mytab';
import Slideshows from './slideshows'
import Listt from './List';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch ,Link} from 'react-router-dom'

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

const Home=()=>{
    return (
        <div>
            <Link to='/searchbaarr'>  
               <div>
                   <Search />
               </div> 
            </Link>

            <Slideshows />

            <Listt />

            <Mytab />

        </div>
    )
}

export default Home;







