import React from 'react';
import {TabBar} from 'antd-mobile';

const Bottom=()=>{
    return(
        <div
            style={{ 
                position: 'fixed',
                width: '100%', 
                bottom: 0 ,
                height:50,
                padding:0
            }}
        >
            {/* 店铺 */}
            <div style={{
                float:"left",
                width:'16%',
                height:'100%',
                
            }}>
                <i className='iconfont icon-dianpu'
                style={{width:'100%',height:'60%'}}></i>
                <span>店铺</span>
            </div>

            {/* 客服 */}
            <div style={{
                float:"left",
                width:'16%',
                height:'100%'
            }}>
            <i className='iconfont icon-kefu'
            style={{width:'100%',height:'60%'}}></i>
                <span>客服</span>
            </div>

            {/* 收藏 */}
            <div style={{
                float:"left",
                width:'16%',
                height:'100%'
            }}>
                <i className='iconfont icon-shoucang'
                style={{width:'100%',height:'60%'}}></i>
                <span>收藏</span>
            </div>

            <div style={{
                width:'52%',
                float:"left",
                backgroundColor:"darkorange",
                borderRadius:10,
                height:'100%',
                textAlign:'center',
            }}>
                {/* 加入购物车 */}
                <div style={{
                    float:"left",
                    width:'50%',
                    height:'100%',
                    color:'#fff',
                    fontSize:17,
                    lineHeight:'45px'
                }}>
                    <span>加入购物车</span>
                </div>

                {/* 立即购买 */}
                <div style={{
                    float:"left",
                    width:'50%',
                    height:'100%',
                    color:'#fff',
                    fontSize:17,  
                    lineHeight:'45px'                  
                }}>
                    <span>立即购买</span>
                </div>
            </div>

            
        </div>
    );
}

export default Bottom