import { SearchBar, TabBar } from 'antd-mobile';
import React from 'react';
import Mytab from './Mytab';
import Home from './Home'
import Cart from './Cart'
import Friend from './Friend'
import My from './My'
import More from './More'
import searchbaarr from './searchbaarr'
import Detail from './detail'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'

const App=()=>{
    return (
        <Router>
            <Switch>
                <Route exact path='/' component={Home} />
                <Route path='/searchbaarr' component={searchbaarr} />
                <Route path='/detail' component={Detail} />
                <Route path='/cart' component={Cart} />
                <Route path='/friend' component={Friend} />
                <Route path='/my' component={My} />
                <Route path='/more' component={More} />
            </Switch>
        </Router>
        
    )
}

export default App;